.card {
  width: 22rem;
  margin-top: 10vmin;
}

.card .card-text {
  padding: 0.5em;
}

.card .card-body a {
  font-size: 1.5em;
}

.carousel-item {
  padding-right: 1.5rem;
}

.border-radius {
  border-radius: 340px;
  width: 60%;
}

@media (max-width: 768px) {
  .card {
    width: 22rem;
  }
}
