.section-t .container-t {
  padding: 5rem;
}

.section-t .row .panel {
  background: white;
  border-radius: 3px;
  padding-top: 10rem;
  padding-bottom: 10rem;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.8);
  font-family: var(--Rubik);
  z-index: 1;
  text-align: center;
}

.section-t .row .panel h1 {
  font-size: 80px;
  color: #fa8320;
}

@media (max-width: 768px) {
  .section-t .row .panel h1 {
    font-size: 50px;
  }
}
