@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-in-page .login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}

.login-in-page .form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 50px;
  padding: 45px;
  text-align: center;
  border-radius: 0.5rem;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.login-in-page .form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.login-in-page .form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #333333;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.login-in-page .form button:hover,
.login-in-page .form button:active,
.login-in-page .form button:focus {
  background: #333333b7;
}
.login-in-page .form .message {
  margin: 15px 0 0;
  color: #333333;
  font-size: 12px;
}
.login-in-page .form .message a {
  color: #333333;
  text-decoration: none;
}
.login-in-page .form .register-form {
  display: none;
}
.login-in-page .container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.login-in-page .container:before,
.login-in-page .container:after {
  content: "";
  display: block;
  clear: both;
}
.login-in-page .container .info {
  margin: 50px auto;
  text-align: center;
}
.login-in-page .container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.login-in-page .container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.login-in-page .container .info span a {
  color: #000000;
  text-decoration: none;
}
.login-in-page .container .info span .fa {
  color: #ef3b3a;
}
.login-in-page body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
