section .main-heading h1 {
  padding-top: 5;
  padding-bottom: 5rem;
  font-size: 10vmin;
}

section .col-md-6 .profile img {
  opacity: 0.9;
  width: 90%;
  border-radius: 0.5em;
}

section .row-aboutus {
  left: -18vmin;
  background: white;
  border-radius: 3px;
  padding: 5vmin 5vmin 5vmin 5vmin;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.8);
  font-family: var(--Rubik);
  z-index: 1;
}

section {
  padding: 2vmin 0;
  padding-bottom: 10vmin;
}

section .row .col-md-6:last-child {
  position: relative;
}

section .row .col-md-6 .panel {
  position: absolute;
  top: 7vmin;
  left: -18vmin;
  background: white;
  border-radius: 3px;
  text-align: left;
  padding: 5vmin 5vmin 5vmin 5vmin;
  padding-bottom: 15vmin;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.2);
  font-family: var(--Rubik);
  z-index: 1;
}

section .video {
  padding-top: 5rem;
}

section .video .video-actual {
  width: 1010px;
  height: 500px;
}

section .video .video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

section .video .video-container iframe,
section .video .video-container object,
section .video .video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  section .row .col-md-6 .panel {
    position: initial;
    width: initial;
    height: initial;
  }
  /* section .video .video-actual {
    width: 275px;
    height: 250px;
  } */
}
