header a {
  font-family: var(--Sniglet-font);
  font-size: 0.9em;
  color: whitesmoke;
}

header .row .col-md-5 img {
  width: 90%;
}

header a:hover {
  color: black;
}

header {
  background-image: url("https://raw.githubusercontent.com/hiteshchoudhary/LCO-sports-web-challenge/master/img3.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

header .nav-item:last-child {
  padding-right: 10.5em;
}

header .nav-item {
  padding: 0.9em;
}

header .navbar-brand {
  padding-left: 8rem;
}

header .row .col-md-7 {
  padding: 30vmin 1vmin;
  padding-bottom: 35vmin;
  font-family: var(--Rubik);
  color: white;
}

header .row .col-md-5 {
  padding: 18vmin 1vmin;
}

header container {
  margin-top: 10rem;
}

header .container .col-md-7 h1 {
  font-size: 8vmin;
  font-weight: bold;
  padding: 0.1em 0em;
  letter-spacing: 5px;
}

header .container .col-md-7 p {
  padding: 1vmin 5vmin;
  font-weight: bold;
  letter-spacing: 2px;
}

header .container .col-md-7 button {
  border-radius: 30px;
  font-weight: bold;
}

@media (max-width: 768px) {
  header .row .col-md-7 {
    padding-bottom: 0;
  }
}
