* {
  box-sizing: border-box;
}

.faqpage h1 {
  font-size: 10vmin;
  padding-top: 5vmin;
  padding-bottom: 5rem;
  text-align: center;
}

.container {
  width: 75%;
  max-width: 80rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.accordian-item {
  border-radius: 10px;
  border-radius: 0.4rem;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0.5rem 2px 0.5rem rgba(0, 0, 0, 0.6);
  padding: 0;
}

.accordian-link {
  font-size: 1.6rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: grey;
}

.accordian-link h6 {
  color: white;
  font-size: 1.6rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
}

.accordian-link i {
  color: white;
  padding: 0.5rem;
}

.accordian-link .fa-minus {
  display: none;
}

.answer {
  max-height: 0;
  overflow: hidden;
  position: relative;
  transition: max-height 1s ease-out;
}

.answer p {
  font-size: 1.4rem;
  color: black;
  padding: 2rem;
}

.ques {
  border-radius: 0.5rem;
}

.answer::before {
  content: "";
  position: absolute;
  width: 0.6rem;
  height: 80%;
  background-color: grey;
  top: -30%;
  left: 0;
  padding-right: 1px;
  transform: translateY(50%);
}

.accordian-item:hover .answer {
  max-height: 20rem;
  background-color: white;
}

.accordian-item {
  background: white;
}

.accordian-item:hover .accordian-link .fa-plus {
  display: none;
}

.accordian-item:hover .accordian-link .fa-minus {
  display: block;
}

@media (max-width: 768px) {
  .accordian-link h6 {
    font-size: 19px;
  }
  .answer p {
    font-size: 15px;
    color: black;
    padding: 2rem;
  }
}
