nav a {
  font-size: 1.2em;
  color: whitesmoke;
}

nav a:hover {
  color: black;
}

nav {
  background: #333333;
}

nav .nav-item:last-child {
  padding-right: 10.5em;
}

nav .nav-item {
  padding-right: 0.9rem;
}

nav .navbar-brand {
  padding-left: 8rem;
  align-items: center;
  color: antiquewhite;
}

@media (max-width: 768px) {
  nav .navbar-brand {
    padding-left: 1rem;
  }
  nav .navbar-nav {
    text-align: center;
  }
  nav .nav-item:last-child {
    padding-right: 0.9em;
  }
}
