  background: var(--bgbasketball);
}

section {
  background: var(--bgbasketball);
} */

header a {
  font-family: var(--Sniglet-font);
  font-size: 0.9em;
  color: whitesmoke;
}

header .row .col-md-5 img {
  width: 90%;
}

header a:hover {
  color: black;
}

header {
  background-image: url("https://raw.githubusercontent.com/hiteshchoudhary/LCO-sports-web-challenge/master/img3.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

header .nav-item:last-child {
  padding-right: 10.5em;
}

header .nav-item {
  padding: 0.9em;
}

header .navbar-brand {
  padding-left: 8rem;
}

header .row .col-md-7 {
  padding: 30vmin 1vmin;
  padding-bottom: 35vmin;
  font-family: var(--Rubik);
  color: white;
}

header .row .col-md-5 {
  padding: 18vmin 1vmin;
}

header container {
  margin-top: 10rem;
}

header .container .col-md-7 h1 {
  font-size: 8vmin;
  font-weight: bold;
  padding: 0.1em 0em;
  letter-spacing: 5px;
}

header .container .col-md-7 p {
  padding: 1vmin 5vmin;
  font-weight: bold;
  letter-spacing: 2px;
}

header .container .col-md-7 button {
  border-radius: 30px;
  font-weight: bold;
}

@media (max-width: 768px) {
  header .row .col-md-7 {
    padding-bottom: 0;
  }
}

section .container h1 {
  font-size: 10vmin;
  padding-top: 4vmin;
  padding-bottom: 0rem;
}

section .team {
  padding-left: 20vmin;
  padding-right: 20vmin;
}

@media (max-width: 768px) {
  section .team {
    padding-left: 10vmin;
    padding-right: 10vmin;
  }
}

.card {
  width: 22rem;
  margin-top: 10vmin;
}

.card .card-text {
  padding: 0.5em;
}

.card .card-body a {
  font-size: 1.5em;
}

.carousel-item {
  padding-right: 1.5rem;
}

.border-radius {
  border-radius: 340px;
  width: 60%;
}

@media (max-width: 768px) {
  .card {
    width: 22rem;
  }
}

section .main-heading h1 {
  padding-top: 5;
  padding-bottom: 5rem;
  font-size: 10vmin;
}

section .col-md-6 .profile img {
  opacity: 0.9;
  width: 90%;
  border-radius: 0.5em;
}

section .row-aboutus {
  left: -18vmin;
  background: white;
  border-radius: 3px;
  padding: 5vmin 5vmin 5vmin 5vmin;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.8);
  font-family: var(--Rubik);
  z-index: 1;
}

section {
  padding: 2vmin 0;
  padding-bottom: 10vmin;
}

section .row .col-md-6:last-child {
  position: relative;
}

section .row .col-md-6 .panel {
  position: absolute;
  top: 7vmin;
  left: -18vmin;
  background: white;
  border-radius: 3px;
  text-align: left;
  padding: 5vmin 5vmin 5vmin 5vmin;
  padding-bottom: 15vmin;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.2);
  font-family: var(--Rubik);
  z-index: 1;
}

section .video {
  padding-top: 5rem;
}

section .video .video-actual {
  width: 1010px;
  height: 500px;
}

section .video .video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

section .video .video-container iframe,
section .video .video-container object,
section .video .video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  section .row .col-md-6 .panel {
    position: static;
    position: initial;
    width: auto;
    width: initial;
    height: auto;
    height: initial;
  }
  /* section .video .video-actual {
    width: 275px;
    height: 250px;
  } */
}

* {
  box-sizing: border-box;
}

.faqpage h1 {
  font-size: 10vmin;
  padding-top: 5vmin;
  padding-bottom: 5rem;
  text-align: center;
}

.container {
  width: 75%;
  max-width: 80rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.accordian-item {
  border-radius: 10px;
  border-radius: 0.4rem;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0.5rem 2px 0.5rem rgba(0, 0, 0, 0.6);
  padding: 0;
}

.accordian-link {
  font-size: 1.6rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: grey;
}

.accordian-link h6 {
  color: white;
  font-size: 1.6rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
}

.accordian-link i {
  color: white;
  padding: 0.5rem;
}

.accordian-link .fa-minus {
  display: none;
}

.answer {
  max-height: 0;
  overflow: hidden;
  position: relative;
  transition: max-height 1s ease-out;
}

.answer p {
  font-size: 1.4rem;
  color: black;
  padding: 2rem;
}

.ques {
  border-radius: 0.5rem;
}

.answer::before {
  content: "";
  position: absolute;
  width: 0.6rem;
  height: 80%;
  background-color: grey;
  top: -30%;
  left: 0;
  padding-right: 1px;
  transform: translateY(50%);
}

.accordian-item:hover .answer {
  max-height: 20rem;
  background-color: white;
}

.accordian-item {
  background: white;
}

.accordian-item:hover .accordian-link .fa-plus {
  display: none;
}

.accordian-item:hover .accordian-link .fa-minus {
  display: block;
}

@media (max-width: 768px) {
  .accordian-link h6 {
    font-size: 19px;
  }
  .answer p {
    font-size: 15px;
    color: black;
    padding: 2rem;
  }
}

nav a {
  font-size: 1.2em;
  color: whitesmoke;
}

nav a:hover {
  color: black;
}

nav {
  background: #333333;
}

nav .nav-item:last-child {
  padding-right: 10.5em;
}

nav .nav-item {
  padding-right: 0.9rem;
}

nav .navbar-brand {
  padding-left: 8rem;
  align-items: center;
  color: antiquewhite;
}

@media (max-width: 768px) {
  nav .navbar-brand {
    padding-left: 1rem;
  }
  nav .navbar-nav {
    text-align: center;
  }
  nav .nav-item:last-child {
    padding-right: 0.9em;
  }
}

footer {
  background: rgba(0, 0, 0, 0.8);
  overflow-x: hidden;
  padding: 5vmin 18vmin;
  padding-bottom: 3vmin;
}

footer h1 {
  font-size: 3rem;
}

footer input {
  border: none !important;
}

footer input::-webkit-input-placeholder {
  color: white !important;
}

footer input:-ms-input-placeholder {
  color: white !important;
}

footer input::-ms-input-placeholder {
  color: white !important;
}

footer input::placeholder {
  color: white !important;
}

footer .input-group .input-group-text {
  background: var(--bggradient);
  border: none;
}

footer .column i {
  color: #dd2476;
}

footer .column i {
  padding-right: 1.15rem;
}

.login-in-page .login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}

.login-in-page .form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 50px;
  padding: 45px;
  text-align: center;
  border-radius: 0.5rem;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.login-in-page .form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.login-in-page .form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #333333;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  transition: all 0.3 ease;
  cursor: pointer;
}
.login-in-page .form button:hover,
.login-in-page .form button:active,
.login-in-page .form button:focus {
  background: #333333b7;
}
.login-in-page .form .message {
  margin: 15px 0 0;
  color: #333333;
  font-size: 12px;
}
.login-in-page .form .message a {
  color: #333333;
  text-decoration: none;
}
.login-in-page .form .register-form {
  display: none;
}
.login-in-page .container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.login-in-page .container:before,
.login-in-page .container:after {
  content: "";
  display: block;
  clear: both;
}
.login-in-page .container .info {
  margin: 50px auto;
  text-align: center;
}
.login-in-page .container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.login-in-page .container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.login-in-page .container .info span a {
  color: #000000;
  text-decoration: none;
}
.login-in-page .container .info span .fa {
  color: #ef3b3a;
}
.login-in-page body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.section-t .container-t {
  padding: 5rem;
}

.section-t .row .panel {
  background: white;
  border-radius: 3px;
  padding-top: 10rem;
  padding-bottom: 10rem;
  box-shadow: 0px 25px 42px rgba(0, 0, 0, 0.8);
  font-family: var(--Rubik);
  z-index: 1;
  text-align: center;
}

.section-t .row .panel h1 {
  font-size: 80px;
  color: #fa8320;
}

@media (max-width: 768px) {
  .section-t .row .panel h1 {
    font-size: 50px;
  }
}

